'use client'

import Link from 'next/link';
import { useState, useCallback, useEffect } from 'react';
import { usePathname } from 'next/navigation';

export default function Header() {
  const [isOpen, setIsOpen] = useState(false);
  const [isVisible, setIsVisible] = useState(true);
  const [lastScrollY, setLastScrollY] = useState(0);
  const pathname = usePathname();

  // Handle scroll events
  useEffect(() => {
    const controlNavbar = () => {
      const currentScrollY = window.scrollY;
      
      if (currentScrollY > lastScrollY && currentScrollY > 100) {
        // Scrolling down & past threshold
        setIsVisible(false);
        setIsOpen(false); // Close mobile menu when hiding header
      } else {
        // Scrolling up
        setIsVisible(true);
      }
      
      setLastScrollY(currentScrollY);
    };

    window.addEventListener('scroll', controlNavbar);
    return () => window.removeEventListener('scroll', controlNavbar);
  }, [lastScrollY]);

  const toggleMenu = useCallback(() => {
    setIsOpen(prev => !prev);
  }, []);

  const menuItems = [
    { name: 'Blog', href: '/' },
    { name: 'Photos', href: '/content/photos' },
    { name: 'Music', href: '/content/music' },
    { name: 'Videos', href: '/content/videos' },
    { name: 'Writings', href: '/content/writtings' }
  ];

  return (
    <header 
      className={`sticky top-0 z-50 w-full border-b border-gray-200 bg-white/90 backdrop-blur-lg transform-gpu transition-all duration-300
        ${isVisible ? 'translate-y-0' : '-translate-y-full'}`}
    >
      <div className="container mx-auto px-4 sm:px-6 lg:px-8 flex justify-between items-center h-14 sm:h-16">
        {/* Mobile Menu and Logo */}
        <div className="flex items-center gap-2 sm:gap-4">
          {/* Mobile Dropdown */}
          <div className="lg:hidden">
            <button 
              className="p-1.5 sm:p-2 text-gray-600 hover:text-gray-600 transition-colors rounded-lg hover:bg-gray-200/50"
              onClick={toggleMenu}
              aria-label="Toggle menu"
            >
              <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 sm:h-6 sm:w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
              </svg>
            </button>
            {isOpen && (
              <div className="absolute left-0 top-14 sm:top-16 w-full bg-white/100 backdrop-blur-xl border-b border-gray-200 shadow-lg transform-gpu animate-fade-in">
                <nav className="container mx-auto px-4 py-2">
                  <ul className="space-y-1 sm:space-y-2">
                    {menuItems.map((item) => (
                      <li key={item.name}>
                        <Link
                          href={item.href}
                          className={`block py-2.5 px-3 transition-colors rounded-lg hover:bg-gray-100/60 ${
                            pathname === item.href
                              ? 'text-gray-900 bg-gray-100/60'
                              : 'text-gray-600 hover:text-gray-900'
                          }`}
                          onClick={toggleMenu}
                        >
                          {item.name}
                        </Link>
                      </li>
                    ))}
                  </ul>
                </nav>
              </div>
            )}
          </div>

          {/* Logo/Brand */}
          <Link 
            href="/" 
            className="text-lg sm:text-xl font-medium text-gray-900 hover:text-gray-600 transition-colors"
          >
            Felix Arash
          </Link>
        </div>

        {/* Desktop Menu */}
        <nav className="hidden lg:block">
          <ul className="flex items-center gap-4 xl:gap-8">
            {menuItems.map((item) => (
              <li key={item.name}>
                <Link
                  href={item.href}
                  className={`px-3 py-2 transition-colors rounded-lg hover:bg-gray-100/50 ${
                    pathname === item.href
                      ? 'text-gray-900 bg-gray-100/50'
                      : 'text-gray-600 hover:text-gray-900'
                  }`}
                >
                  {item.name}
                </Link>
              </li>
            ))}
          </ul>
        </nav>
      </div>
    </header>
  );
}